import React from 'react';
import {Menu} from 'antd';
import propTypes from 'prop-types';

const {Item,SubMenu,}=Menu;

class TopicsListMenu extends React.Component{

    static contextTypes={
      handleChangeTopicsList:  propTypes.func
    };

    render(){
        return (
            <Menu
                mode={"horizontal"}
                onClick={({key})=>this.context.handleChangeTopicsList(key)}
            >
                <Item key={"all"}></Item>
                <Item key={"good"}></Item>
                <Item key={"share"}></Item>
                <Item key={"ask"}></Item>
                <Item key={"job"}></Item>
                <Item key={"test"}></Item>
                <SubMenu title={""}>
                    <Item></Item>
                    <Item>Python</Item>
                    <Item>iDev</Item>
                    <Item>Android</Item>
                    <Item>Linux</Item>
                    <Item>node.js</Item>
                    <Item></Item>
                    <Item></Item>
                </SubMenu>
                <SubMenu title={""}>
                    <Item></Item>
                    <Item></Item>
                    <Item></Item>
                </SubMenu>
                <SubMenu title={""}>
                    <Item></Item>
                    <Item></Item>
                    <Item></Item>
                    <Item></Item>
                    <Item></Item>
                    <Item></Item>
                </SubMenu>
                <SubMenu title={"Apple"}>
                    <Item>macOS</Item>
                    <Item>iPhone</Item>
                    <Item>iPad</Item>
                    <Item>MBP</Item>
                    <Item>iMac</Item>
                    <Item>iWatch</Item>
                    <Item>Apple</Item>
                </SubMenu>
            </Menu>
        )
    }
}

export default TopicsListMenu;